* {
  margin: 0;
  padding: 0;
  font-family: 'Book Antiqua';
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  color: #fff;
  background: #222222;
}

.banner-container {
  background: url('https://github.com/piyush-eon/react-crypto-tracker/blob/master/public/banner2.jpg?raw=true');

  background-size: cover;
  background-position: center;
}

.banner-content {
  text-align: center;
  min-height: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 75px 0 20px;
}

.banner-content .text {
  color: #eeeeeeb3;
  margin-top: 22px;
  font-size: 18px;
  text-transform: capitalize;
  word-spacing: 2px;
}

.carousel-container {
  margin-top: 5%;
  display: flex;
  align-items: center;
}

.carousel-container .carousel-item {
  width: 200px;
  max-width: 90%;
}

.carousel-item .image {
  width: 120px;
  margin: auto;
}

.carousel-item .image img {
  width: 100%;
  height: 100%;
}

.carousel-item .desc {
  display: flex;
  flex-direction: column;
  color: #fff;
  margin-top: 30px;
}

.desc .profit {
  margin-bottom: 17px;
  text-transform: uppercase;
  color: #ffffff;
  word-spacing: 2px;
  margin-right: 5px;
}

.desc .green {
  color: #00FFCA;
}

.desc .red {
  color: #D21312;
}

.desc .price {
  line-height: 3rem;
  font-size: 27px;
  font-weight: bold;
}

.coins-container {
  margin: 30px 0;
}

.coins-container .input-search-field {
  width: 60%;
  margin: 30px auto;
  max-width: 100%;
}

::-webkit-scrollbar {
  display: none;
}

/* COIN PAGE */
.coinPageContainer {
  margin: 6% 20px 0;
  display: flex;
  align-items: center;
}

.coinPageContainer .sidebar {
  flex: 30%;
  height: auto;
  padding: 0 14px;
}

.coinPageContainer .chart {
  flex: 65%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 19px;
}

.sidebar .header {
  display: block;
  text-align: center;
}

.sidebar .image {
  width: 200px;
  margin: 20px auto;
}

.sidebar .image>img {
  width: 100%;
}

.sidebar .heading {
  text-align: center;
}

.sidebar .description {
  text-align: justify;
  margin: 30px 0;
  line-height: 1.6rem;
  font-size: 16px;
}

.sidebar .content>div {
  margin: 24px 0;
  display: flex;
  align-items: center;
}


.sidebar .content>div .ans {
  margin-left: 16px;
  font-size: 20px;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* CHART CONTAINER */
.chartContainer {
  width: 100%;
}

.chartContainer .buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin-top: 30px;
}

.buttons .button {
  border: 1px solid #eebc1d;
  color: #fff;
  padding: 4px 30px;
}

.buttons .button:hover,
.buttons .button.selected {
  background: #fff102;
  color: #191919;
  font-weight: bold;
}

@media (max-width: 1025px) {
  .coinPageContainer {
    flex-direction: column;
    margin-top: 28%;
  }

  .coinPageContainer .sidebar {
    flex: 100%;
    border-right: 0;
  }

  .buttons .button {
    margin: 10px;
  }

  .banner-content {
    padding-top: 90px;
  }

}